.wp-block-carkeek-blocks-rollover-images {
  text-align: center; }
  .wp-block-carkeek-blocks-rollover-images .rollover-images__inner {
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
    max-width: 600px;
    margin-right: auto;
    margin-left: auto; }
  .wp-block-carkeek-blocks-rollover-images .wp-block-carkeek-blocks-rollover-image {
    position: relative;
    cursor: pointer; }
    .wp-block-carkeek-blocks-rollover-images .wp-block-carkeek-blocks-rollover-image figure:last-of-type {
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      transition: opacity 0.3s ease; }
    .wp-block-carkeek-blocks-rollover-images .wp-block-carkeek-blocks-rollover-image:focus {
      outline: none; }
      .wp-block-carkeek-blocks-rollover-images .wp-block-carkeek-blocks-rollover-image:focus figure:last-of-type {
        opacity: 1; }
    .wp-block-carkeek-blocks-rollover-images .wp-block-carkeek-blocks-rollover-image .image-rollover__hover_text {
      display: none; }
  .wp-block-carkeek-blocks-rollover-images .rollover-images__footer {
    min-height: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center; }
  .wp-block-carkeek-blocks-rollover-images.has-1-columns .wp-block-carkeek-blocks-rollover-image {
    flex-basis: 100%; }
  .wp-block-carkeek-blocks-rollover-images.has-2-columns .wp-block-carkeek-blocks-rollover-image {
    flex-basis: 50%; }
  .wp-block-carkeek-blocks-rollover-images.has-3-columns .wp-block-carkeek-blocks-rollover-image {
    flex-basis: 33%; }
  .wp-block-carkeek-blocks-rollover-images.has-4-columns .wp-block-carkeek-blocks-rollover-image {
    flex-basis: 25%; }
  .wp-block-carkeek-blocks-rollover-images.has-5-columns .wp-block-carkeek-blocks-rollover-image {
    flex-basis: 20%; }
  .wp-block-carkeek-blocks-rollover-images.has-6-columns .wp-block-carkeek-blocks-rollover-image {
    flex-basis: 16.66666%; }
  .wp-block-carkeek-blocks-rollover-images.rollovertext-above {
    display: flex;
    flex-direction: column; }
    .wp-block-carkeek-blocks-rollover-images.rollovertext-above .rollover-images__footer {
      order: 2;
      margin-bottom: 2rem; }
    .wp-block-carkeek-blocks-rollover-images.rollovertext-above .rollover-images__inner {
      order: 3; }

.wp-block-carkeek-blocks-rollover-images.venn-diagram {
  cursor: pointer; }
  .wp-block-carkeek-blocks-rollover-images.venn-diagram .wp-block-image {
    margin-bottom: 0; }
  .wp-block-carkeek-blocks-rollover-images.venn-diagram .rollover-images__inner {
    position: relative;
    width: 600px;
    margin-right: auto;
    margin-left: auto; }
    .wp-block-carkeek-blocks-rollover-images.venn-diagram .rollover-images__inner .wp-block-carkeek-blocks-rollover-image:not(.venn-default) {
      position: absolute;
      top: 0;
      left: 0;
      display: none;
      z-index: 0; }
    .wp-block-carkeek-blocks-rollover-images.venn-diagram .rollover-images__inner .wp-block-carkeek-blocks-rollover-image.venn-default {
      position: relative;
      z-index: 1; }
    .wp-block-carkeek-blocks-rollover-images.venn-diagram .rollover-images__inner .wp-block-carkeek-blocks-rollover-image.current {
      display: block;
      z-index: 5; }
    .wp-block-carkeek-blocks-rollover-images.venn-diagram .rollover-images__inner .venn-zone {
      position: absolute;
      height: 380px;
      width: 380px;
      border-radius: 100%; }
      .wp-block-carkeek-blocks-rollover-images.venn-diagram .rollover-images__inner .venn-zone:focus {
        outline: none; }
    .wp-block-carkeek-blocks-rollover-images.venn-diagram .rollover-images__inner .zone-venn-top {
      z-index: 10;
      top: 0;
      left: 50%;
      transform: translateX(-50%); }
    .wp-block-carkeek-blocks-rollover-images.venn-diagram .rollover-images__inner .zone-venn-bottom-left {
      z-index: 20;
      position: absolute;
      left: 0;
      bottom: 0; }
    .wp-block-carkeek-blocks-rollover-images.venn-diagram .rollover-images__inner .zone-venn-bottom-right {
      z-index: 30;
      position: absolute;
      right: 0;
      bottom: 0; }

@media screen and (max-width: 650px) {
  .wp-block-carkeek-blocks-rollover-images.venn-diagram {
    padding-left: 0;
    padding-right: 0; }
    .wp-block-carkeek-blocks-rollover-images.venn-diagram .rollover-images__inner {
      width: 300px; }
      .wp-block-carkeek-blocks-rollover-images.venn-diagram .rollover-images__inner .venn-zone {
        height: 190px;
        width: 190px; }
    .wp-block-carkeek-blocks-rollover-images.venn-diagram .rollover-images__footer {
      padding-left: 1.5rem;
      padding-right: 1.5rem; } }
