@charset "UTF-8";
/* Slider */
.slick-loading .slick-list {
  background: #fff url(img/ajax-loader.gif) center center no-repeat; }

/* Icons */
/* Arrows */
.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  height: 25px;
  width: 25px;
  line-height: 0px;
  font-size: 0px;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 50%;
  transform: translate(0, -50%);
  padding: 0;
  border: none;
  outline: none;
  z-index: 200; }
  .slick-prev:hover, .slick-prev:focus,
  .slick-next:hover,
  .slick-next:focus {
    outline: none;
    background: transparent;
    color: transparent; }
    .slick-prev:hover:before, .slick-prev:focus:before,
    .slick-next:hover:before,
    .slick-next:focus:before {
      opacity: 1; }
  .slick-prev.slick-disabled:before,
  .slick-next.slick-disabled:before {
    opacity: 0.75; }
  .slick-prev:before,
  .slick-next:before {
    font-family: "carkeek-blocks";
    font-size: 30px;
    line-height: 1;
    color: white;
    opacity: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale; }

.slick-prev {
  left: 7px; }
  [dir="rtl"] .slick-prev {
    left: auto;
    right: 7px; }
  .slick-prev:before {
    content: ""; }
    [dir="rtl"] .slick-prev:before {
      content: ""; }

.slick-next {
  right: 7px; }
  [dir="rtl"] .slick-next {
    left: 7px;
    right: auto; }
  .slick-next:before {
    content: ""; }
    [dir="rtl"] .slick-next:before {
      content: ""; }

@media (min-width: 1024px) {
  .slick-prev {
    left: 20px; }
    [dir="rtl"] .slick-prev {
      left: auto;
      right: 20px; }
  .slick-next {
    right: 20px; }
    [dir="rtl"] .slick-next {
      left: 20px;
      right: auto; } }

/* Dots */
.slick-dotted.slick-slider {
  margin-bottom: 30px; }

.slick-dots {
  position: absolute;
  bottom: 25px;
  list-style: none;
  display: block;
  text-align: right;
  padding: 0 50px 0 0;
  margin: 0;
  width: 100%; }
  .slick-dots li {
    position: relative;
    display: inline-block;
    height: 20px;
    width: 20px;
    margin: 0 5px;
    padding: 0;
    cursor: pointer; }
    .slick-dots li button {
      border: 0;
      background: transparent;
      display: block;
      height: 20px;
      width: 20px;
      outline: none;
      line-height: 0px;
      font-size: 0px;
      color: transparent;
      padding: 5px;
      cursor: pointer; }
      .slick-dots li button:hover, .slick-dots li button:focus {
        outline: none; }
        .slick-dots li button:hover:before, .slick-dots li button:focus:before {
          opacity: 1; }
      .slick-dots li button:before {
        position: absolute;
        top: 0;
        content: ' ';
        width: 10px;
        height: 10px;
        border: 1px solid #fff;
        border-radius: 100%;
        line-height: 20px;
        text-align: center;
        color: #fff;
        opacity: 0.75;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale; }
    .slick-dots li.slick-active button:before {
      background-color: #fff;
      opacity: 1; }

.slick-slide {
  color: #fff; }
  .slick-slide .wp-block-media-text img {
    width: 100%;
    height: 66vw;
    max-height: 800px;
    object-fit: cover; }
  .slick-slide .wp-block-media-text .wp-block-media-text__content {
    padding-left: 4rem;
    padding-right: 4em; }

.slick-initialized .slick-track {
  display: flex; }
  .slick-initialized .slick-track .slick-slide {
    flex: 1 1 0;
    display: flex;
    height: auto;
    align-items: center;
    justify-content: center; }
    .slick-initialized .slick-track .slick-slide > div,
    .slick-initialized .slick-track .slick-slide .slide-single {
      height: 100%; }
      .slick-initialized .slick-track .slick-slide > div > [class^="wp-block"],
      .slick-initialized .slick-track .slick-slide .slide-single > [class^="wp-block"] {
        height: 100%; }

.wp-block-carkeek-blocks-slider__slide-wrapper {
  height: 66vw; }

.wp-block-carkeek-blocks-image-slider .slick-list,
.wp-block-carkeek-blocks-image-slider .slick-track {
  height: 100%; }

.wp-block-carkeek-blocks-image-slider .slick-slide > div {
  width: 100%; }

.wp-block-carkeek-blocks-image-slider .carkeek-slider-item {
  width: 100%;
  height: 100%;
  position: relative; }

.wp-block-carkeek-blocks-image-slider .carkeek-slider-item__image {
  width: 100%;
  height: 100%;
  background-size: cover; }

.wp-block-carkeek-blocks-image-slider .carkeek-slider-item__caption {
  position: absolute;
  left: 0;
  bottom: 0;
  padding: 1rem 2rem;
  font-size: 1.5rem; }
